import React, { useEffect, useState } from "react";
import Navigation from "../Navigation/Navigation";
import Search from "../Search/Search";
import firebase from "../../assets/firebase/firebase";
import "firebase/database";
import MealItem from "../FoodBook/MealItem/MealItem";

const FavouriteMeals = () => {
  const [meals, setMeals] = useState([]);
  const [currentUser, setCurrentUser] = useState("");

  const favouriteMealsList = meals.map((item) => (
    <MealItem
      key={item.id}
      id={item.id}
      fat={item.fat}
      protein={item.protein}
      carbs={item.carbs}
      typeOfMeal={item.typeOfMeal}
      src={item.src}
      difficulty={item.difficulty}
      time={item.time}
    />
  ));

  useEffect(() => {
    firebase.auth().onAuthStateChanged((user) => {
      if (user) {
        setCurrentUser(user.uid);
      }
    });

    const ref = firebase
      .database()
      .ref("/users/" + currentUser + "/favourites");
    const watch = () => {
      ref.on("value", (snapshot) => {
        const meals = snapshot.val();
        const newMeals = [];
        for (let id in meals) {
          newMeals.push({ id, ...meals[id] });
        }
        const favouriteMeals = newMeals.filter(
          (item) => item.favourite === true
        );
        setMeals(favouriteMeals);
      });
    };
    watch();
    return watch();
  }, [currentUser]);

  return (
    <>
      <Search />
      <div className="FavouriteMeals">
        <h1 className="FavouriteMeals__header">Ulubione</h1>
        {meals[0] ? (
          <ul className="FavouriteMeals__listOfFavourites">
            {favouriteMealsList}
          </ul>
        ) : (
          <p className="FavouriteMeals__emptyList">Nic jeszcze nie dodano</p>
        )}
      </div>
      <Navigation />
    </>
  );
};

export default FavouriteMeals;
